.displayWall {
    width: 100vw;
    min-height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding-top: 30px;
    padding-bottom: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.workLink {
    width: 100%;
    height: 100%;
    text-decoration:none;
}

.workLink > div{
    width: 100%;
    height: 100%;
}
.workLink > div div {
    transform: translateY(40px);
    transition: all 1s;

}

.workLink:hover > div:hover div {
    transform: translateY(0px);
}

.contents {
    /* border: 1px solid #919866; */
    text-align: center;
    display: inline-block;

    width: 90%;
    margin-bottom: 20px;

    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-items: center;
}

.work{
    border: 1px solid #919866;
    text-align: center;
    

    width: 80%;
    height: 200px;
    margin-bottom: 20px;
    width: 300px;

    
}
.contents > div {
    transition: all 1s;

    border-radius: 15px;
    overflow: hidden;
}
.contents > div:hover {
    box-shadow: 0px 0px 40px rgb(12 12 12 / 40%);
    transform: scale(1.06, 1.06);
}

@media screen and (max-width: 1050px) {
    .work {
        width: 200px;
        height: 133.333333333px;
    }
}

@media screen and (max-width: 900px) {
    .contents {
        grid-template-columns: repeat(2, 1fr);

    }
}
@media screen and (max-width: 500px) {
    .work {
        width: 130px;
        height: 87px;
    }
    .contents {
        grid-row-gap: 0px;
        grid-column-gap: 0px;
    }
}
